import tuiIcon from "@/components/icon/icon"
import tuiModal from "@/components/modal/modal"
export default {
	components: {
		tuiIcon,
		tuiModal
	},
	data() {
		return {
			userProvider:'',
			userInfo: '',
			showModal: true,
			// 订单类型
			totalNum:0,
			orderList: [
				{
					text: '待付款',
					icon: "fukuan",
					id: "0",
					num: 0
				},
				{
					text: '待发货',
					icon: "fahuo",
					id: "1",
					num: 0
				},
				{
					text: '待收货',
					icon: "shouhuo",
					id: "3",
					num: 0
				},
				// {
				// 	text: '已收货',
				// 	icon: "pingjia",
				// 	id: "4",
				// 	num: 0
				// },
				{
					text: '退款',
					icon: "tuihuo",
					id: "8",
					num: 0
				}
			],
			// 工具栏列表
			mytoolbarList: [{
					url: '../../user/address/address',
					text: '收货地址',
				},
				
				{
					url: '../../user/keep/keep',
					text: '我的收藏'
				},
				{
					url: '',
					text: '团购专享'
				},
				{
					url: '',
					text: '关于我们'
				},
			],
			// 购物车数量
			count: 0,
			cnum:0, //优惠券数量
		}
	},
	onLoad() {
		this.userProvider=this.$xm.userProvider();
	},
	onShow() {
		this.getcount();
		this.getCoupon()
		uni.getStorage({
			key: 'cart',
			success: (res) => {
				this.count = res.data;
			}
		})

		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				console.log(res)
				this.userInfo = res.data;
				if (!this.userInfo) {
					if(this.userProvider=='weixin'){
						this.showModal = true
					}else{
						uni.login({
							success: (res) => {
								if (res.code) {
									var code = res.code
									uni.getUserInfo({
										success: (res) => {	
											this.userInfo = res.userInfo;
											let data = {
												encryptedData: res.encryptedData,
												iv: res.iv
											}
											this.$xm.post('/Index/getUnionid', data, (res) => {
												const user = uni.getStorageSync('user');
												user.unionid=res.unionid;
												uni.setStorage({
													key: 'user',
													data: user,
													success: (res) => {
														console.log(res);
													}
												});
											})
											uni.setStorage({
												key: 'userInfo',
												data: res.userInfo,
												success: (res) => {
													console.log(res);
												}
											});
						
										},
										fail: res => {
											console.log(res)
											// 获取失败的去引导用户授权 
										}
									})
						
								} else {}
							}
						})
					}
					
				} else {
					this.showModal = false;
				}
			}
		})

	},
	methods: {
		getuserinfo() {
			// wx登录
			uni.login({
				success: (res) => {
					console.log(res)
					if (res.code) {
						var code = res.code
						uni.getUserInfo({
							success: (res) => {
								this.userInfo = res.userInfo;
								this.showModal = false;
								let data = {
									encryptedData: res.encryptedData,
									iv: res.iv
								}
								this.$xm.post('/Index/getUnionid', data, (res) => {
									const user = uni.getStorageSync('user');
									user.unionid=res.unionid;
									uni.setStorage({
										key: 'user',
										data: user,
										success: (res) => {
											console.log(res);
										}
									});
								})
								uni.setStorage({
									key: 'userInfo',
									data: res.userInfo,
									success: (res) => {
										console.log(res);
									}
								});

							},
							fail: res => {
								console.log(res)
								// 获取失败的去引导用户授权 
							}
						})

					} else {}
				}
			})
		},
		toOrderList(index) {
			if (!index) {
				index = "s"
			}
			console.log(index)
			uni.setStorageSync('tbIndex', index);
			uni.navigateTo({
				url: '../../user/order_list/order_list?tbIndex=' + index
			})
		},
		// 获取订单shuliang
		getcount() {				
			this.$xm.post('/Mine/index', '', (res) => {
				let result = res.order_state;
				let ids=result.map(ele=>{
					return ele.order_state
				})
				let num=0;
				result.forEach(ele=>{
					num+=Number(ele.state);
				});
				this.totalNum=num;
				ids = ids.toString().replace(/,/g, "");
				this.orderList.forEach(ele => {						
					if (ids.indexOf(ele.id) !== -1) {							
						let index=ids.indexOf(ele.id);							
						ele.num=result[index].state
					} else {
						ele.num=0
					}
				})
			})
		},
		// 获取优惠券
		getCoupon(){
			let data={
				type:1
			};
			this.$xm.post('/Coupon/index',data,(res)=>{
				this.cnum=res.data.length
			})
		},
		toCart() {
			uni.switchTab({
				url: '../cart/cart'
			})
		},
		toPage(url) {
			console.log(url);
			if (!url) {
				uni.showToast({
					title: '此内容正在开发中',
					icon: "none"
				});
				return;
			}
			uni.navigateTo({
				url: url
			})
		},
		close() {
			this.showModal = false;
		}
	}
}
